<template>
  <div class="c-character-intro">
    <div class="c-character-intro__avatar" :class="[avatarClass, { 'has-avatar': !!person && !!person.headUrl }]" @click="clickAvatar">
      <div v-if="!!person && !!person.headUrl" class="c-avatar__icon">
        <i :style="{ backgroundImage: `url(${ person.headUrl })` }"></i>
      </div>
    </div>
    <div class="c-character-intro__content">
      <div v-if="!!person && !!person.name">
        <div class="c-character-intro__wrapper"><span v-overflow="person.name" class="c-character-intro__name" :class="{ 'is-kid': person.isKid }">{{ person.name }}</span></div>
        <div v-if="person.info" class="c-character-intro__info">{{ person.info }}</div>
        <div v-if="person.tag" class="c-character-intro__tag" :class="{ 'is-kid': person.isKid }">{{ person.tag }}</div>
      </div>
    </div>
    <div class="c-character-intro__extend">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'character-intro',
  props: {
    person: {
      type: Object
    }
  },
  computed: {
    avatarClass () {
      return this.person && this.person.isKid ? this.person.isMale ? 'c-avatar--boy' : 'c-avatar--girl' : 'c-avatar'
    }
  },
  methods: {
    clickAvatar () {
      this.$emit('avatar')
    }
  }
}
</script>

<style lang="scss">
.c-character-intro {
  min-height: 100px;
  display: flex;
  &__avatar, &__extend {
    flex: none;
  }
  &__content {
    flex: 1;
    padding-left: 6px;
    overflow: hidden;
  }
  &__wrapper {
    padding-top: 6px;
    white-space: nowrap;
    overflow: hidden;
  }
  &__name {
    display: inline-block;
    color: #fff;
    font-size: 28px;
    line-height: 1;
    @extend %font-family__din--medium;
    &.is-kid {
      font-size: 30px;
    }
    &.is-overflow {
      font-size: 21px;
    }
  }
  &__info {
    margin-top: 7px;
    color: rgba(#fff, 0.7);
    font-size: 12px;
    line-height: 1;
    white-space: nowrap;
    overflow: hidden;
    @extend %font-family__sf;
  }
  &__tag {
    display: inline-block;
    margin-top: 11px;
    line-height: 23px;
    padding: 0 12px;
    color: #fff;
    font-size: 12px;
    border-radius: 9px;
    background-color: rgba(#fff, 0.3);
    @extend %font-family__sf;
    &.is-kid {
      padding: 0 7px;
      line-height: 16px;
    }
  }
  &--black {
    .c-character-intro__name, .c-character-intro__tag {
      color: #333;
    }
    .c-character-intro__info {
      color: rgba(#333, 0.4);
    }
    .c-character-intro__tag {
      background-color: #eaeaea;
    }
  }
}
</style>
