<template>
  <div class="p-pre-modify-mobile is-form c-modify">
    <div class="p-pre-modify-mobile__nav">
      <div class="o-fixed-tab" :class="{ 'is-active': isPhoneMode }" @click="changeMode(2)">{{ $t('tab.phone') }}</div>
      <div class="o-fixed-tab" :class="{ 'is-active': isEmailMode }" @click="changeMode(3)">{{ $t('tab.email') }}</div>
    </div>
    <template v-if="isPhoneMode">
      <div v-if="hasMobile" class="c-form-field" :class="{ 'is-invalid': !!accountError }">
        <div class="c-form-field__label">{{ $t('tab.oldPhoneNumber') }}</div>
        <div class="c-form-field__input_wrapper">
          <div class="c-form-field__input u-overflow-cut">{{ `+${ areaCode } ${ mobile }` }}</div>
        </div>
        <div class="c-form-field__tip">{{ accountError }}</div>
      </div>
      <form-field v-else type="number" class="c-form-field--select" :label="$t('tab.oldPhoneNumber')" :error="accountError" v-model="mobile">
        <area-code :areaCode="areaCode" @change="pick"></area-code>
      </form-field>
    </template>
    <template v-else>
      <div v-if="hasEmali" class="c-form-field" :class="{ 'is-invalid': !!accountError }">
        <div class="c-form-field__label">{{ $t('mobile.oldEmail') }}</div>
        <div class="c-form-field__input_wrapper">
          <div class="c-form-field__input u-overflow-cut">{{ email }}</div>
        </div>
        <div class="c-form-field__tip">{{ accountError }}</div>
      </div>
      <form-field v-else class="c-form-field" :label="$t('tab.oldEmail')" :placeholder="$t('tab.oldEmail')" :error="accountError" v-model="email">
      </form-field>
    </template>
    <form-field class="c-form-field--button" :label="$t('sms.vCode')" :error="vCodeError" v-model="vCode">
      <sms :mode="mode" :areaCode="areaCode" :mobile="mobile" :email="email" :analysisError="analysisError" @send="clearError"></sms>
    </form-field>
    <div class="o-button o-button--submit t-adapt-button--active c-modify__submit" :class="{ 'is-active': isValid }" @click="next">{{ $t('common.next') }}</div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import * as RetCode from '@/code/return-code.js'
import formField from '@/components/FormField.vue'
import areaCode from '@/widgets/AreaCode.vue'
import sms from '@/widgets/Sms.vue'

export default {
  name: 'pre-modify-mobile',
  components: {
    formField,
    areaCode,
    sms
  },
  data () {
    return {
      hasMobile: false,
      hasEmali: false,
      mode: 2,
      areaCode: '',
      mobile: '',
      email: '',
      vCode: '',
      accountErrorInfo: null,
      vCodeErrorInfo: null
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff']),
    url () {
      switch (this.mode) {
        case 2:
          return 'staff/mobile/checkvcode'
        case 3:
          return 'staff/email/checkvcode'
        default:
          return ''
      }
    },
    isPhoneMode () {
      return this.mode === 2
    },
    isEmailMode () {
      return this.mode === 3
    },
    checkType () {
      return this.isStaff ? 2 : 1
    },
    mobileVal () {
      return this.areaCode && this.mobile ? this.areaCode + this.mobile : null
    },
    account () {
      return this.isPhoneMode ? this.mobileVal : this.email
    },
    isValid () {
      return this.account && this.vCode
    },
    accountError () {
      return this.accountErrorInfo ? this.$lang.code[this.accountErrorInfo.code] || this.accountErrorInfo.message : ''
    },
    vCodeError () {
      return this.vCodeErrorInfo ? this.$lang.code[this.vCodeErrorInfo.code] || this.vCodeErrorInfo.message : ''
    }
  },
  methods: {
    clearError () {
      this.accountErrorInfo = null
      this.vCodeErrorInfo = null
    },
    changeMode (mode) {
      if (this.mode !== mode) {
        this.clearError()
        this.vCode = ''
        this.mode = mode
        if (this.$httpPost) {
          this.$httpPost.setUrl(this.url)
        }
      }
    },
    pick (areaCode) {
      this.areaCode = areaCode
    },
    next () {
      if (this.isValid) {
        this.clearError()
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost(this.url, {
            opts: {
              lock: true
            },
            success: () => {
              this.$router.replace({ name: 'modify-mobile' })
            },
            fail: error => {
              switch (error.code) {
                case RetCode.ERROR_VCODE_UNSET:
                case RetCode.ERROR_VCODE_INCORRECT:
                  this.vCodeErrorInfo = {
                    code: error.code,
                    message: error.message
                  }
                  return true
                default:
                  return false
              }
            }
          })
        }
        this.$httpPost.request(this.isPhoneMode ? {
          token: this.user.token,
          mobile: this.mobileVal,
          vCode: this.vCode
        } : {
          token: this.user.token,
          email: this.email,
          vCode: this.vCode
        })
      }
    },
    analysisError (error) {
      switch (error.code) {
        case RetCode.ERROR_MOBILE_NOT_RERISTERED:
        case RetCode.ERROR_EMAIL_NOT_RERISTERED:
          this.accountErrorInfo = {
            code: error.code,
            message: error.message
          }
          return true
        default:
          return false
      }
    }
  },
  created () {
    if (this.user.areaCode && this.user.mobilePhone) {
      this.areaCode = this.user.areaCode
      this.mobile = this.user.mobilePhone
      this.hasMobile = true
    }
    if (this.user.email) {
      this.email = this.user.email
      this.hasEmali = true
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

<style lang="scss">
.p-pre-modify-mobile {
  &__nav {
    margin-bottom: 40px;
    text-align: center;
    .o-fixed-tab + .o-fixed-tab {
      margin-left: 115px;
    }
  }
}
</style>
