<template>
  <div class="p-question">
    <div class="p-question__title">{{ faq.question }}</div>
    <div class="p-question__content" v-html="faq.content"></div>
  </div>
</template>

<script>
export default {
  name: 'question',
  props: ['faq'],
  created () {
    if (!this.faq) {
      this.$router.replace({ name: 'faq' })
    }
  }
}
</script>

