import '@/scss/index.scss'
import 'swiper/dist/css/swiper.min.css'

import Vue from 'vue'
import globals from '@/globals.js'
import inject from '@/inject.js'
import App from '@/App.vue'
import store, { initialize } from '@/store/index.js'
import router from '@/router.js'
import i18n from '@/locale/index.js'
import JsNativeApi from '@/utils/js-native-api.js'

Promise.all([inject(), initialize()]).then(result => {
  let account = result[1]
  if (account) {
    JsNativeApi.register(account)
  }
  new Vue({
    store,
    router,
    i18n,
    render: h => h(App)
  }).$mount('#app')
})
