<template>
  <navigation-wrapper :title="title" class="p-faq">
    <div style="height: 15px;"></div>
    <div class="l-navigation__content u-overflow-hidden" ref="wrapper">
      <div>
        <ul ref="listWrapper">
          <li v-for="(faq, index) in faqs" :key="index" class="p-faq__item" @click="choose(faq)">
            <div class="p-faq__label u-more">{{ faq.question }}</div>
          </li>
        </ul>
        <div v-if="canPullUp" class="p-sign-in-history__pullup">{{ pullUpTip }}</div>
      </div>
      <loading :fetching="loading" :isError="isError" :retry="fetch"></loading>
      <div v-if="empty" class='p-sign-in-history__time'>{{ $t('common.empty') }}</div>
    </div>
    <router-view class="p-faq__question" :faq="faq" />
  </navigation-wrapper>
</template>

<script>
import { mapState } from 'vuex'
import { getRect } from '@/utils/dom.js'
import BScroll from 'better-scroll'
import loading from '@/components/Loading.vue'

export default {
  name: 'faq',
  components: {
    loading
  },
  data () {
    return {
      faqs: [],
      httpPost: null,
      page: 1,
      pageSize: 20,
      isPullUpLoad: false,
      error: false,
      hasMore: true,
      hasFetched: false,
      faq: null
    }
  },
  computed: {
    ...mapState(['user']),
    title () {
      return this.$route.name === 'faq' ? this.$t('home.menu.faq') : ''
    },
    fetching () {
      return this.httpPost && this.httpPost.isRequesting()
    },
    loading () {
      return this.fetching && !this.hasFetched
    },
    isError () {
      return this.error && !this.hasFetched
    },
    has () {
      return this.faqs.length > 0
    },
    empty () {
      return this.hasFetched && !this.has
    },
    canPullUp () {
      return this.hasFetched && this.has
    },
    pullUpTip () {
      return this.isPullUpLoad ? this.$t('pullUp.loading') : this.hasMore ? this.$t('pullUp.more') : this.$t('pullUp.finished')
    }
  },
  methods: {
    initScroll () {
      if (!this.$refs.wrapper || !this.has || this.$scroll) {
        return
      }
      this.$refs.listWrapper.style.minHeight = `${ getRect(this.$refs.wrapper).height + 1 }px`
      this.$scroll = new BScroll(this.$refs.wrapper, {
        scrollY: true,
        click: true,
        probeType: 2,
        pullUpLoad: {
          threshold: 10
        }
      })
      this.$scroll.on('pullingUp', () => {
        if (this.hasMore && !this.fetching) {
          this.isPullUpLoad = true
          this.fetch()
        } else {
          this.$scroll.finishPullUp()
        }
      })
    },
    pullUpFnished () {
      if (this.isPullUpLoad) {
        this.isPullUpLoad = false
        this.$scroll.finishPullUp()
      }
    },
    forceUpdate () {
      this.$nextTick(() => {
        if (this.$scroll) {
          this.pullUpFnished()
          this.$scroll.refresh()
        } else {
          this.initScroll()
        }
      })
    },
    add (faqs) {
      this.hasFetched = true
      if (faqs.length < this.pageSize) {
        this.hasMore = false
      }
      if (faqs.length === 0) {
        return
      }
      this.faqs = this.faqs.concat(faqs)
      this.page += 1
    },
    fetch () {
      this.httpPost.request({
        token: this.user.token,
        page: this.page,
        pageSize: this.pageSize
      })
    },
    choose (faq) {
      this.faq = faq
      this.$router.push({ name: 'question' })
    }
  },
  watch: {
    fetching (nVal, oVal) {
      if (oVal && !nVal) {
        setTimeout(() => {
          this.forceUpdate()
        }, 20)
      }
    }
  },
  created () {
    this.httpPost = this.$http.createPost('sundry/faq/loadAll', {
      success: (res) => {
        this.error = false
        this.add(res.data)
      },
      fail: () => {
        this.error = true
      }
    })
    this.fetch()
  },
  mounted () {
    this.initScroll()
  },
  beforeDestroy () {
    if (this.httpPost) {
      this.httpPost.cancel()
    }
    if (this.$scroll) {
      this.$scroll.destroy()
    }
  }
}
</script>

<style lang="scss">
.p-faq {
  &__item {
    position: relative;
    padding: 12px 17px 12px 20px;
    background-color: #fff;
    @include border-bottom-1px(#e6e6e6);
  }
  &__label {
    position: relative;
    padding-right: 10px;
    color: #404040;
    font-size: 14px;
    line-height: 25px;
    word-break: break-all;
    @extend %font-family__sf;
  }
  &__question {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #fff;
  }
  .l-safe-area__content {
    position: relative;
    background-color: #f2f2f2;
  }
}
</style>
