<template>
  <navigation-wrapper class="c-sign-in-information" :class="{ 'is-teacher': isTeacher, 'is-student': isStudent }" :title="$t('signIn.title')">
    <div class="l-navigation__content">
      <div class="c-sign-in-information__wrapper">
        <character-intro class="c-sign-in-information__intro c-character-intro--black" :person="person">
          <div class="c-sign-in-information__phone_wrapper">
            <div class="c-sign-in-information__phone" @click="ringUp"></div>
          </div>
        </character-intro>
        <div v-if="canManual" class="c-sign-in-information__operation c-sign-in">
          <div class="c-sign-in__item c-sign-in__status">{{ $t('signIn.status') }}</div>
          <div class="c-sign-in__item c-sign-in__type">{{ $t('signInType.2') }}<i class="c-sign-in__icon" :class="{ 'c-sign-in__icon--checked': isManual }" @click="toggleSignIn(1)"></i></div>
          <div class="c-sign-in__item c-sign-in__type">{{ $t('signInType.3') }}<i class="c-sign-in__icon" :class="{ 'c-sign-in__icon--checked': isLeave }" @click="toggleSignIn(3)"></i></div>
          <div v-if="isLeave" class="c-sign-in__reason">
            <div class="c-sign-in__reason_item" :class="{ 'is-active': reasonType === 1 }" @click="changeReason(1)">
              <i class="c-sign-in__reason_radio"></i>{{ $t('reason.sick') }}
            </div>
            <div class="c-sign-in__reason_item" :class="{ 'is-active': reasonType === 3 }" @click="changeReason(3)">
              <i class="c-sign-in__reason_radio"></i>{{ $t('reason.personal') }}
            </div>
            <div class="c-sign-in__reason_item" :class="{ 'is-active': reasonType === 2 }" @click="changeReason(2)">
              <i class="c-sign-in__reason_radio"></i>{{ $t('reason.pd') }}
            </div>
            <div class="c-sign-in__reason_item" :class="{ 'is-active': reasonType === 4 }" @click="changeReason(4)">
              <i class="c-sign-in__reason_radio"></i>{{ $t('reason.other') }}
            </div>
          </div>
        </div>
        <div class="c-sign-in-information__data">
          <div class="c-sign-in-data" @click="toHistory('normal')">
            <div class="c-sign-in-data__count">{{ normal }}</div>
            <div v-once class="c-sign-in-data__label">{{ $t('signIn.normal') }}</div>
          </div>
          <div class="c-sign-in-data" @click="toHistory('abnormal')">
            <div class="c-sign-in-data__count c-sign-in-data__count--prominent">{{ abnormal }}</div>
            <div v-once class="c-sign-in-data__label">{{ $t('signIn.abnormal') }}</div>
          </div>
        </div>
      </div>
    </div>
  </navigation-wrapper>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { Gender } from '@/enum/common.js'
import { formatToYYYYMMDD } from '@/utils/format.js'
import * as Event from '@/code/custom-event.js'
import characterIntro from '@/views/components/CharacterIntro.vue'

export default {
  name: 'sign-in-infomation',
  components: {
    characterIntro
  },
  data () {
    return {
      person: null,
      phoneNumber: null,
      normal: 0,
      abnormal: 0,
      signInType: 0, // 0 1 2 3
      submitType: 0,
      reasonType: 1,
      submitReasonType: 1
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isAdmin', 'isHeadTeacher']),
    mode () {
      return this.$route.params.mode
    },
    isTeacher () {
      return this.mode === 'teacher'
    },
    isStudent () {
      return this.mode === 'student'
    },
    id () {
      return Number(this.$route.params.id)
    },
    info () {
      return decodeURI(this.$route.params.info)
    },
    isMale () {
      return this.$route.params.gender !== Gender.FEMALE.toLowerCase()
    },
    isValid () {
      return this.isAdmin || this.isStudent && this.isHeadTeacher
    },
    canManual () {
      return this.isStudent && this.isHeadTeacher || this.isTeacher && this.isAdmin
    },
    isManual () {
      return this.signInType === 1
    },
    isLeave () {
      return this.signInType === 3
    }
  },
  methods: {
    analysis (data) {
      this.person = {
        id: this.id,
        name: data.firstName + (data.lastName ? ` ${ data.lastName }` : ''),
        headUrl: data.headUrl,
        info: this.isStudent ? data.parentEmail : data.email,
        tag: this.info,
        isKid: this.isStudent,
        isMale: this.isMale // data.gender === Gender.MALE
      }
      this.phoneNumber = this.isStudent ? data.areaCode + data.parentMobile : null
      this.normal = data.accReachCount
      this.abnormal = data.accAbsenceCount
    },
    submit () {
      if (!this.$siginInHttpPost) {
        this.$siginInHttpPost = this.$http.createPost(this.isStudent ? 'teacher/student/signin' : 'admin/staff/signin', {
          opts: {
            lock: true
          },
          success: () => {
            switch (this.signInType) {
              case 1:
                this.normal = Math.max(0, this.normal - 1)
                break
              case 3:
                this.abnormal = Math.max(0, this.abnormal - 1)
                break
              default:
                break
            }
            this.signInType = this.submitType
            switch (this.signInType) {
              case 1:
                this.normal += 1
                this.submitReasonType = 1
                break
              case 3:
                this.abnormal += 1
                break
              default:
                this.submitReasonType = 1
                break
            }
            this.reasonType = this.submitReasonType
          },
          fail: () => {
            this.submitType = 0
          }
        })
      }
      let postData = {
        token: this.user.token,
        type: this.submitType,
        signDate: formatToYYYYMMDD(new Date())
      }
      if (this.isStudent) {
        postData.idStudent = this.id
      } else {
        postData.idStaff = this.id
      }
      if (this.submitType === 3) {
        postData.reasonType = this.submitReasonType
      }
      this.$siginInHttpPost.force(postData)
    },
    signIn (type) {
      if (!this.person || this.submitType === type) {
        return
      }
      this.submitType = type
      this.submit()
    },
    changeReason (type) {
      if (!this.person || type === this.submitReasonType) {
        return
      }
      this.submitType = 3
      this.submitReasonType = type
      this.submit()
    },
    toggleSignIn (type) {
      if (this.signInType === type) {
        this.signIn(0)
      } else {
        this.signIn(type)
      }
    },
    toHistory (type) {
      if (this.person) {
        this.$router.push({ name: 'attendance-history', params: { mode: this.mode, id: this.id, type } })
      }
    },
    ringUp () {
      if (this.phoneNumber) {
        this.$native.ringUp(this.phoneNumber)
      } else {
        this.$eventBus.$emit(Event.TOAST, {
          code: -1,
          message: this.$t('error.nomobile')
        })
      }
    }
  },
  created () {
    if (this.isStudent) {
      this.person = {
        isKid: true,
        isMale: this.isMale
      }
    }
    if (this.isValid) {
      this.$httpPost = this.$http.createPost(this.isStudent ? 'teacher/student/info' : 'admin/staff/info', {
        success: res => this.analysis(res.data)
      })
      this.$httpPost.request(this.isStudent ? {
        token: this.user.token,
        idStudent: this.id
      } : {
        token: this.user.token,
        idStaff: this.id
      })
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
    if (this.$siginInHttpPost) {
      this.$siginInHttpPost.cancel()
    }
  }
}
</script>

<style lang="scss">
.c-sign-in-information {
  background-color: #f2f2f2;
  background-size: 100% 262.5px;
  background-repeat: no-repeat;
  &.is-teacher {
    @include bg-img-ratio-normal('../../assets/teacher_bg');
    .c-sign-in__reason_item.is-active .c-sign-in__reason_radio {
      border-color: #497cf3;
      &::after {
        content: '';
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: #497cf3;
        @extend %position-center;
      }
    }
  }
  &.is-student {
    @include bg-img-ratio-normal('../../assets/child_bg');
    .c-sign-in__reason_item.is-active .c-sign-in__reason_radio {
      border-color: #49c58e;
      &::after {
        content: '';
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: #49c58e;
        @extend %position-center;
      }
    }
  }
  &__wrapper {
    position: relative;
    margin: 0 15px;
    padding: 25px 15px;
    border-radius: 6px;
    background-color: #fff;
  }
  &__phone_wrapper {
    width: 58px;
    text-align: right;
  }
  &__phone {
    display: inline-block;
    position: relative;
    top: 6px;
    right: 11px;
    @include bg-img-ratio('../../assets/phone', 73, 73);
  }
  &__intro {
    padding-bottom: 20px;
  }
  &__data {
    padding-top: 25px;
    text-align: center;
  }
  .c-navigation-bar {
    color: #fff;
  }
  .c-navigation-bar__icon {
    @include bg-img-ratio-normal('../../assets/back2');
  }
}
.c-sign-in {
  padding: 15px;
  border-radius: 6px;
  background-color: #f2f2f2;
  &__item {
    position: relative;
    color: #333;
    & + & {
      margin-top: 26px;
    }
  }
  &__status {
    font-size: 16px;
    @extend %font-family__sf--medium;
  }
  &__type {
    font-size: 14px;
    @extend %font-family__pingfang;
  }
  &__reason {
    position: relative;
    margin-top: 13px;
    font-size: 0;
    @include border-top-1px(#e6e6e6);
  }
  &__reason_item {
    position: relative;
    display: inline-block;
    box-sizing: border-box;
    width: 50%;
    padding-left: 14px;
    color: rgba(#333, 0.5);
    font-size: 12px;
    line-height: 40px;
    @extend %font-family__pingfang;
    .c-sign-in__reason_radio {
      box-sizing: border-box;
      left: 0;
      width: 12px;
      height: 12px;
      border: 1px solid #bfbfbf;
      border-radius: 50%;
      @extend %vertical-middle;
    }
    &.is-active {
      color: #333;
      .c-sign-in__reason_radio {
        color: #333;
      }
    }
  }
  &__icon {
    right: 0;
    @include bg-img-ratio('../../assets/unchecked', 34, 34);
    @extend %vertical-middle;
    &--checked {
      @include bg-img-ratio-normal('../../assets/checked');
    }
  }
}
.c-sign-in-data {
  display: inline-block;
  text-align: center;
  &__count {
    padding-bottom: 14px;
    color: #343434;
    font-size: 50px;
    height: 36px;
    line-height: 36px;
    @extend %font-family__dinpro;
    &--prominent {
      color: #fe6652;
    }
  }
  &__label {
    color: rgba(#333, 0.5);
    font-size: 12px;
    @extend %font-family__sf;
  }
  & + & {
    margin-left: 90px;
  }
}
</style>
