<template>
  <div class="c-form-field__button" :class="{ 'is-active': count > 0 }" @click="send"><span>{{ msg }}</span></div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'sms',
  props: {
    mobile: {
      type: String
    },
    areaCode: {
      type: String
    },
    email: {
      type: String
    },
    mode: { // 0 1  2  3 
      type: Number,
      default: 0
    },
    type: { // 1  2 
      type: Number,
      default: 1
    },
    analysisError: {
      type: Function,
      default: null
    }
  },
  data () {
    return {
      count: 0,
    }
  },
  computed: {
    ...mapState(['user']),
    msg () {
      return this.count > 0 ? this.$t('sms.countdown', { count: this.count }) : this.$t('sms.send')
    },
    url () {
      switch (this.mode) {
        case 0:
        case 2:
          return 'common/smscode/send'
        case 1:
        case 3:
          return 'common/email/send'
        default:
          return null
      }
    }
  },
  methods: {
    cacnel () {
      if (this.timer !== -1) {
        this.count = 0
        clearInterval(this.timer)
        this.timer = -1
      }
      if (this.$httpPost) {
        this.$httpPost.cancel()
      }
    },
    clock () {
      this.count = 60
      this.timer = setInterval(() => {
        this.count -= 1
        if (this.count === 0) {
          clearInterval(this.timer)
          this.timer = -1
          this.$httpPost.awaken()
        }
      }, 1000)
    },
    sendToMobile () {
      if (!this.areaCode || !this.mobile) {
        return
      }
      if (this.$httpPost.isWait()) {
        this.clock()
        let data = {
          areaCode: this.areaCode,
          mobile: this.mobile,
          type: this.type
        }
        if (this.mode === 2) {
          data.token = this.user.token
        }
        this.$httpPost.request(data)
      }
    },
    sendToEmali () {
      if (!this.email) {
        return
      }
      if (this.$httpPost.isWait()) {
        this.clock()
        let data = {
          email: this.email,
          type: this.type
        }
        if (this.mode === 3) {
          data.token = this.user.token
        }
        this.$httpPost.request(data)
      }
    },
    send () {
      this.$emit('send')
      if (!this.$httpPost) {
        this.$httpPost = this.$http.createPost(this.url, {
          opts: {
            sleep: true
          },
          fail: error => {
            if (this.count > 0) {
              this.count = 0
              clearInterval(this.timer)
              this.timer = -1
            }
            return !!this.analysisError && this.analysisError(error)
          }
        })
      }
      switch (this.mode) {
        case 0:
        case 2:
          this.sendToMobile()
          break
        case 1:
        case 3:
          this.sendToEmali()
          break
        default:
          break
      }
    }
  },
  watch: {
    mode () {
      if (this.$httpPost) {
        this.$httpPost.setUrl(this.url)
        this.cacnel()
      }
    }
  },
  beforeDestroy () {
    this.cacnel()
  }
}
</script>

