import Store from '@/store/index.js'
import http from '@/utils/http.js'
import EventBus from '@/utils/event-bus.js'
import JsNativeApi from '@/utils/js-native-api.js'
import { log } from '@/utils/log.js'
import * as SystemEvent from '@/code/system-event.js'
import * as Event from '@/code/custom-event.js'

let cache = {}

function hasChecked (id) {
  if (!cache[id]) {
    cache[id] = { status: 0 }
  }
  if (cache[id] && cache[id].status !== 0) {
    return true
  }
  return false
}

function checkSteps (mac) {
  if (!Store.getters.isLogined) {
    return
  }
  let isStaff = Store.getters.isStaff
  let figure
  if (isStaff) {
    figure = Store.state.figures.find(figure => figure.macAddress === mac)
  } else {
    figure = Store.state.figures.find(figure => figure.braceletMac === mac)
  }
  if (figure && !hasChecked(figure.id)) {
    let token = Store.state.user.token
    let id = figure.id
    cache[id].status = 1
    JsNativeApi.getBraceletHealthAcitvity().then(result => {
      if (result.type === 'true') {
        let nativeSteps = Number(result.data.step || 0)
        http.post(isStaff ? 'staff/sport' : 'parent/child/sport', isStaff ? {
          token
        } : {
          token,
          idStudent: id
        }, {
          ignore: true,
          prevent: true
        }).then(res => {
          let steps = res.data.steps || 0
          if (nativeSteps > steps) {
            http.post(isStaff ? 'staff/steps/update' : 'parent/child/steps/update', isStaff ? {
              token,
              steps: nativeSteps
            } : {
              token,
              idStudent: id,
              steps: nativeSteps
            }, {
              ignore: true,
              prevent: true
            }).then(() => {
              log(`${ id } update server steps finished: native > server`)
              cache[id].status = 0
            }, error => {
              log(`${ id } update server steps failed: ${ error.message }`)
              cache[id].status = 0
            })
          } else {
            log(`${ id } update server steps finished: native <= server`)
            cache[id].status = 0
          }
        }, error => {
          log(`${ id } fetch server steps failed: ${ error.message }`)
          cache[id].status = 0
        })
      } else {
        log(`${ id } fetch native steps failed`)
        cache[id].status = 0
      }
    }, () => {
      log(`${ id } fetch native steps failed: unknown`)
      cache[id].status = 0
    })
  }
}

let bgUrl = null
function savePic (url) {
  let canvas = document.createElement('canvas')
  let ctx = canvas.getContext('2d')
  let img = new Image()
  img.crossOrigin = 'Anonymous'
  img.onload = function () {
    if (url === bgUrl) {
      canvas.width = img.width
      canvas.height = img.height
      ctx.drawImage(img, 0, 0)
      EventBus.$emit(Event.SAVE_PIC_SUCCESS, url)
      try {
        window.localStorage.setItem('smart_campus__bg', canvas.toDataURL('image/jpg'))
        log(`bg load success: ${ url }`)
      } catch (e) {
        log(`bg save failed, ${ url }: ${ e.message }`)
      }
    }
    canvas = null
    ctx = null
    img = null
  }
  img.onerror = function () {
    if (url === bgUrl) {
      bgUrl = null
      log(`bg load failed: ${ url }`)
    }
    canvas = null
    ctx = null
    img = null
  }
  img.onabort = function () {
    if (url === bgUrl) {
      bgUrl = null
      log(`bg load abort: ${ url }`)
    }
    canvas = null
    ctx = null
    img = null
  }
  img.src = url
}

export function listen () {
  EventBus.$on(Event.LOGIN_OUT, () => {
    cache = {}
  })

  EventBus.$on(Event.BRACELET_CONNECTED, mac => {
    checkSteps(mac)
  })

  EventBus.$on(SystemEvent.BRACELET_CONNECTED, data => {
    if (data && data.macAddress) {
      checkSteps(data.macAddress)
    }
  })

  EventBus.$on(Event.SAVE_PIC, url => {
    if (bgUrl !== url) {
      bgUrl = url
      savePic(url)
    }
  })
}
