<template>
  <navigation-wrapper :title="$t('busMap.title')">
    <!-- <google-map class="l-navigation__content" :center="center" :zoom="7">
      <google-marker v-for="(m, index) in markers" :key="index" :position="m.position" :clickable="true" :draggable="true" @click="center=m.position"></google-marker>
    </google-map> -->
    <div class="l-navigation__content c-bus-map" ref="map"></div>
    <div class="c-bus">
      <div class="c-bus-info c-bus__info">
        <div class="c-bus-info__logo c-bus-logo">
          <div class="c-bus-logo__icon"></div>
        </div>
        <div class="c-bus-info__no">No.</div>
        <div v-if="routeNo" class="c-bus-info__route">{{ routeNo }}</div>
      </div>
      <div class="c-bus__time">
        <div class="c-bus-time">
          <div class="c-bus-time__label">{{ $t('busMap.boarding') }}</div>
          <div class="c-bus-time__value">{{ boarding }}</div>
        </div>
        <div class="c-bus-time">
          <div class="c-bus-time__label">{{ $t('busMap.unloading') }}</div>
          <div class="c-bus-time__value">{{ unloading }}</div>
        </div>
      </div>
    </div>
  </navigation-wrapper>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { log } from '@/utils/log.js'

let hasLoaded = false
let googleMapScript = null
let success = null

function asyncLoadGoogleMap (cb) {
  if (!hasLoaded && !googleMapScript) {
    googleMapScript = document.createElement('script')
    googleMapScript.type = 'text/javascript'
    googleMapScript.src = 'http://ditu.google.cn/maps/api/js?&key=AIzaSyBU7s-e3nAYl12Tu0o4DykAiSz3d6AekPQ'
    // googleMapScript.src = 'https://maps.googleapis.com/maps/api/js?&key=AIzaSyBU7s-e3nAYl12Tu0o4DykAiSz3d6AekPQ'
    document.body.appendChild(googleMapScript)
    googleMapScript.onload = () => {
      log('load google-map success')
      hasLoaded = true
      googleMapScript = null
      success && success()
    }
    googleMapScript.onerror = () => {
      log('load google-map error')
      hasLoaded = false
      document.body.removeChild(googleMapScript)
      googleMapScript = null
    }
  }
  if (hasLoaded) {
    cb()
  } else {
    success = cb
  }
}

function cancelLoadGoogleMap () {
  success = null
  if (googleMapScript && googleMapScript.abort) {
    googleMapScript.abort()
  }
}


export default {
  name: 'bus-map',
  data () {
    return {
      map: null,
      loaded: false,
      googleMapLoaded: false,
      icon: './wz.png',
      routeNo: '',
      boarding: '',
      unloading: '',
      gaps: []
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff']),
  },
  methods: {
    analysis (data) {
      this.loaded = true
      this.routeNo = data.routeNo
      this.boarding = data.boardingTimeStr
      this.unloading = data.unloadingTimeStr
      this.gaps = data.tripGps.map(val => {
        return {
          lat: Number(val.latitude),
          lng: Number(val.longitude)
        }
      })
      this.draw()
    },
    draw () {
      if (!this.$refs.map || !this.loaded || this.gaps.length === 0) {
        return
      }
      if (!this.googleMapLoaded) {
        asyncLoadGoogleMap(() => {
          this.googleMapLoaded = true
          this.draw()
        })
        return
      }
      let latlngbounds = new google.maps.LatLngBounds()
      this.gaps.forEach(val => {
        latlngbounds.extend(val)
        return val
      })
      let center = latlngbounds.getCenter()
      if (!this.$map) {
        this.$map = new google.maps.Map(this.$refs.map, {
          // zoom: 15,
          center: center,
          mapTypeId: google.maps.MapTypeId.ROADMAP,
          disableDefaultUI: true,
          draggable: false
        })
        this.$marker = new google.maps.Marker({
          position: this.gaps[this.gaps.length - 1],
          icon: {
            url: './wz.png',
            anchor: new google.maps.Point(37.5, 37.5)
          },
          map: this.$map
        })
        this.$polyline = new google.maps.Polyline({
          path: this.gaps,
          geodesic: true,
          strokeColor: '#bebebe',
          strokeWeight: 8,
          icons: [{
            icon: {
              path: google.maps.SymbolPath.FORWARD_OPEN_ARROW,
              strokeColor: '#ddd',
              strokeWeight: 2,
              scale: 0.8
            },
            repeat: '20px'
          }],
          map: this.$map
        })
      } else {
        this.$map.setCenter(center)
        this.$marker.setPosition(this.gaps[this.gaps.length - 1])
        this.$polyline.setPath(this.gaps)
      }
      this.$map.fitBounds(latlngbounds)
    }
  },
  created () {
    this.$httpPost = this.$http.createPost(this.isStaff ? 'staff/bustrip' : 'parent/child/bustrip', {
      opts: {
        cycle: 60000
      },
      success: res => this.analysis(res.data)
    })
    this.$httpPost.request({
      token: this.user.token,
      idBus: this.$route.params.busId,
      checkDetailId: this.$route.params.id
    })
  },
  mounted () {
    this.draw()
  },
  beforeDestroy () {
    cancelLoadGoogleMap()
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

<style lang="scss">
.c-bus-map {
  position: relative;
  background-color: #f2f2f2;
}
.c-bus {
  position: absolute;
  bottom: 15px;
  left: 15px;
  right: 15px;
  padding: 25px 15px 22px 15px;
  border-radius: 6px;
  background-color: #fff;
  box-shadow: 0px 1px 25px 0px rgba(#000, 0.1);
  &__time {
    padding-left: 75px;
  }
}
.c-bus-logo {
  display: inline-block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #fff;
  &__icon {
    display: inline-block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-position: 50% 50%;
    background-size: 36px 31px;
    background-repeat: no-repeat;
    @include bg-img-ratio-normal('../../assets/bus2');
  }
}
.c-bus-info {
  position: absolute;
  top: -30px;
  width: 60px;
  height: 108px;
  background: linear-gradient(135deg, #ffdc2b, #ffa81c);
  border-radius: 30px 30px 6px 6px;
  &__logo {
    position: absolute;
    top: 5px;
    left: 5px;
  }
  &__no {
    position: absolute;
    bottom: 42px;
    left: 4px;
    color: #fff;
    font-size: 10px;
    line-height: 1;
    @extend %font-family__din--medium;
  }
  &__route {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 10px;
    font-size: 40px;
    height: 30px;
    line-height: 30px;
    color: #fff;
    text-align: center;
    @extend %font-family__dinpro;
  }
}
.c-bus-time {
  display: flex;
  color: #333;
  font-size: 16px;
  line-height: 1;
  @extend %font-family__sf--medium;
  &__time {
    flex: none;
  }
  &__value {
    flex: 1;
    text-align: right;
    white-space: nowrap;
    overflow: hidden;
  }
  & + & {
    margin-top: 20px;
  }
}
</style>
