<template>
  <navigation-wrapper :title="$t('home.menu.bracelet')">
    <div class="l-navigation__content has-padding">
      <ul>
        <li v-for="figure in figures" :key="figure.id" class="c-bracelet-item">
          <div class="c-bracelet-item__avatar" :class="[isStaff ? 'c-avatar' : figure.gender === 'Male' ? 'c-avatar--boy' : 'c-avatar--girl', { 'has-avatar': !!figure.headUrl }]">
            <div v-if="!!figure.headUrl" class="c-avatar__icon">
              <i :style="{ backgroundImage: `url(${ figure.headUrl })` }"></i>
            </div>
          </div>
          <div class="c-bracelet-item__name">{{ figure.name }}</div>
          <div class="c-bracelet-item__button" :class="{ 't-adapt-button': figure.braceletOfBinding === 0, 'o-button--prominent': figure.braceletOfBinding === 1 }" @click="toggle(figure)">{{ figure.braceletOfBinding === 0 ? $t('bracelet.bind') : $t('bracelet.unbind') }}</div>
        </li>
      </ul>
    </div>
  </navigation-wrapper>
</template>

<script>
import { mapState, mapGetters } from 'vuex'

export default {
  name: 'bracelet',
  computed: {
    ...mapState(['figures']),
    ...mapGetters(['isStaff']),
    has () {
      return this.figures.length > 0
    }
  },
  methods: {
    toggle (figure) {
      if (figure.braceletOfBinding === 0) {
        this.$router.replace({ name: 'scan', params: { id: figure.id } })
      } else {
        this.$router.replace({ name: 'unbind', params: { id: figure.id } })
      }
    },
    check () {
      if (this.figures.length === 1) {
        this.toggle(this.figures[0])
      }
    }
  },
  watch: {
    has (val) {
      if (val) {
        this.check()
      }
    }
  },
  created () {
    if (this.has) {
      this.check()
    }
  }
}
</script>

<style lang="scss">
.c-bracelet-item {
  position: relative;
  padding: 0 90px 0 80px;
  @include border-bottom-1px(#e6e6e6);
  &__avatar {
    left: 0;
    @extend %vertical-middle;
  }
  &__name {
    height: 100px;
    color: #404040;
    font-size: 18px;
    line-height: 100px;
    white-space: nowrap;
    overflow: hidden;
    @extend %font-family__sf;
  }
  &__button {
    right: 0;
    width: 80px;
    line-height: 30px;
    box-sizing: border-box;
    border-radius: 15px;
    text-align: center;
    color: #fff !important;
    font-size: 14px;
    @extend %vertical-middle;
    @extend %font-family__pingfang;
  }
}
</style>
